body {
  margin: 0;
  padding: 0;
  font-family: "Oswald", sans-serif;
}

h1,
h2,
h3,
p {
  margin: 0;
}

a {
  text-decoration: none;
}

ul {
  list-style-type: none;
  padding: 0;
  margin: 0;
}

img {
  max-width: 100%;
}

.container {
  max-width: 75rem;
  width: 90%;
  margin-inline: auto;
}

.header {
  background-color: #2e323f;
  padding: 2rem 0;
  text-align: center;
}
@media (min-width: 700px) {
  .header {
    padding: 1rem 0;
  }
}
.header__logo {
  height: 30px;
}
@media (min-width: 700px) {
  .header__row {
    display: flex;
  }
}

.nav {
  width: 100%;
}
@media (min-width: 700px) {
  .nav {
    display: flex;
    align-items: center;
    justify-content: flex-end;
  }
}
.nav__list {
  padding: 0;
  margin: 0;
}
@media (min-width: 700px) {
  .nav__list {
    display: flex;
    gap: 2rem;
  }
}
.nav__list--primary {
  margin-bottom: 2em;
}
@media (min-width: 700px) {
  .nav__list--primary {
    margin-bottom: unset;
    margin-right: 2rem;
  }
}
.nav__item {
  margin-top: 0.75em;
}
@media (min-width: 700px) {
  .nav__item {
    margin-top: unset;
  }
}
.nav__item--btn {
  background-color: #a59678;
  border-radius: 100px;
  font-weight: 900;
  padding: 0.3em;
}
.nav__item__link {
  color: white;
  text-transform: uppercase;
  font-size: 1.3rem;
  opacity: 0.7;
}
.nav__item__link:hover, .nav__item__link:focus {
  cursor: pointer;
  opacity: 1;
}

.first {
  background-color: #3b4050;
  padding: 3rem 0;
  text-align: center;
}

.row {
  width: 90%;
  display: flex;
  flex-direction: column;
  gap: 2rem;
}
@media (min-width: 700px) {
  .row {
    flex-direction: row;
  }
}

@media (min-width: 700px) {
  .col__first {
    text-align: left;
    width: 50%;
  }
}
@media (min-width: 700px) {
  .col__second {
    align-self: flex-end;
    text-align: left;
    width: 35%;
  }
}
.col__title {
  color: #ffffff;
  font-size: 2.5rem;
}
@media (min-width: 700px) {
  .col__title {
    font-size: 4rem;
  }
}
.col__title--words {
  color: #a59678;
  display: block;
}
.col__text {
  color: #ffffff;
  font-size: 1.3rem;
  margin-bottom: 3rem;
}
.col__btn {
  background-color: #a59678;
  border-radius: 100px;
  font-weight: 900;
  padding: 0.5em;
  color: #ffffff;
  text-transform: uppercase;
  letter-spacing: 0.05em;
  font-size: 1.3em;
}
.col__btn:hover {
  cursor: pointer;
  opacity: 0.7;
}

.second {
  background-color: rgb(245, 244, 244);
  text-align: center;
  padding: 3rem 0;
}

.row {
  width: 100%;
}

.col__t {
  color: #a59678;
  font-size: 2rem;
  margin-bottom: 1rem;
}
.col__p {
  width: 80%;
  margin: 0 auto;
  font-size: 1.2rem;
}

.main {
  width: 90%;
  padding: 2rem 0;
  text-align: center;
  margin: 0 auto;
}

@media (min-width: 700px) {
  .row__sidebar {
    display: flex;
    flex-direction: column;
  }
}
@media (min-width: 1000px) {
  .row__sidebar {
    display: flex;
    flex-direction: row;
  }
}

.primary-content {
  display: flex;
  flex-direction: column;
  gap: 1rem;
  align-items: center;
}
@media (min-width: 1000px) {
  .primary-content {
    width: 60%;
    margin-right: 10%;
  }
}
.primary-content .section-title {
  color: #a59678;
}
@media (min-width: 700px) {
  .primary-content .section-title {
    text-align: left;
  }
}
.primary-content .section-paragraph {
  text-align: left;
}

.sidebar {
  display: flex;
  flex-direction: column;
  gap: 1rem;
  text-align: center;
  margin: 0 auto;
  margin-top: 1rem;
}
@media (min-width: 700px) {
  .sidebar {
    flex-direction: row;
  }
}
@media (min-width: 1000px) {
  .sidebar {
    flex-direction: column;
    margin-top: unset;
    margin-left: 1rem;
    align-self: center;
    width: 30%;
  }
}
.sidebar .sidebar__item {
  background-color: #3b4050;
  color: #ffffff;
  padding: 3rem 1rem;
}
@media (min-width: 1000px) {
  .sidebar .sidebar__item {
    height: 30%;
  }
}
.sidebar .sidebar-title {
  margin-bottom: 2rem;
  font-size: 1.5rem;
}

.footer {
  background-color: #3b4050;
  padding: 2rem 0;
  text-align: center;
}
.footer__row {
  display: flex;
  flex-direction: column;
  gap: 2rem;
}
@media (min-width: 700px) {
  .footer__row {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    text-align: left;
  }
}
.footer h2 {
  color: #ffffff;
  font-size: 2rem;
  padding: 2rem;
}
@media (min-width: 700px) {
  .footer__company {
    flex-basis: 34%;
  }
}
.footer__item {
  font-family: "Montserrat", sans-serif;
}
.footer__item h3 {
  color: #a59678;
  margin-bottom: 1rem;
}
.footer__item p {
  color: lightgray;
  font-size: 0.75rem;
}
.footer__li {
  margin-bottom: 0.75rem;
}
.footer__link {
  color: white;
  font-size: 0.75rem;
  font-weight: normal;
  opacity: 0.7;
}
.footer__link:hover, .footer__link:focus {
  cursor: pointer;
  opacity: 1;
}